.footer {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	height: 100px;
	@include vertical;
	.footer__in {
		display: inline-block;
		vertical-align: middle;
		font-size: 1.4rem;
		color: $text-color-dark; }
	.footer__links {
		margin-bottom: 5%;
		li {
			display: inline-block;
			margin: 0 15px;
			a {
				font-size: 18px;
				color: $text-color;
				transition: color 300ms;
				&:hover {
					color: $text-color-dark; } } } }
	@include rh(700) {
    height: 70px;
    .footer__links {
      margin-bottom: 0; } }
	@include r(768) {
    position: relative;
    bottom: auto;
    left: auto;
    right: auto;
    height: auto;
    .footer__links {
      margin-bottom: 15px;
      li {
        a {
          font-size: 28px; } } } } }
