// styles
.center {
  position: absolute;
  left: 25%;
  width: 50%;
  margin: 0 auto;
  height: 100%;
  background: rgba($lightblue, 0.2);
  z-index: 9;
  @include r(768) {
    top: auto;
    left: 0;
    bottom: 18px;
    width: 100%;
    height: auto;
    background: none; } }
.container {
  position: absolute;
  top: 100px;
  bottom: 100px;
  left: 50%;
  width: 820px;
  max-width: 80%;
  transform: translateX(-50%);
  background-color: $main;
  box-shadow: 0 0 40px 10px #d2dfee;
  z-index: 10;
  @include rh(700) {
    top: 70px;
    padding-top: 0;
    bottom: 70px; }
  @include r(768) {
    top: 0!important;
    padding-top: 70px!important;
    bottom: 0!important;
    width: 100%;
    max-width: 100%;
    box-shadow: none; } }

.section {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  .section__in {
    position: relative;
    height: 100%;
    padding: 0 15%;
    @include vertical;
    &:after {
      content: attr(data-title);
      position: absolute;
      bottom: -10%;
      left: 2%;
      transform-origin: 0 0;
      transform: rotate(-90deg);
      color: rgba($text-color, 0.07);
      font-size: 8rem;
      text-transform: uppercase;
      font-weight: bold;
      line-height: 1; } }
  @include r(768) {
    padding: 70px 0 0; }
  @include r(480) {
    .section__in {
      padding: 0 10px;
      &:after {
        bottom: auto;
        top: -45px;
        left: 50%;
        font-size: 20px;
        color: $text-color-dark;
        transform: translateX(-50%); } } } }
.title {
	text-align: center;
	font-size: 2.4rem;
	text-transform: uppercase;
	color: $text-color-dark; }

// start
.start {
  position: absolute;
  top: 40px;
  bottom: 40px;
  left: 40px;
  right: 40px;
  border: 4px solid #bddcff;
  text-align: center;
  &:after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    width: 110px;
    margin-top: -2px;
    margin-left: -55px;
    height: 4px;
    background: $border; }
  @include rh(600) {
    top: 20px;
    bottom: 20px;
    left: 20px;
    right: 20px;
    &:before {
      top: 55%; } }
  @include r(768) {
    @include vertical;
    border: none;
    &:after {
      display: none; }
    .start__in {
      display: inline-block;
      vertical-align: middle; } }
  .start__logo {
    position: relative;
    display: none;
    margin-bottom: 40px;
    &:before {
      content: "";
      position: absolute;
      bottom: -19px;
      left: 50%;
      width: 110px;
      margin-top: -2px;
      margin-left: -55px;
      height: 4px;
      background: $border; }
    @include r(768) {
      display: inline-block;
      svg {
        font-size: 10rem; }
      p {
        font-size: 2.4rem;
        font-weight: bold;
        text-transform: uppercase; } } }
  .start__title {
    position: absolute;
    bottom: 27%;
    left: 0;
    right: 0;
    @include rh(600) {
      bottom: 21%;
      font-size: 2rem; }
    @include r(768) {
      position: relative;
      top: auto;
      bottom: auto;
      left: auto;
      right: auto;
      margin-bottom: 25px; } }
  .start__btn {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 19%;
    @include r(768) {
      position: relative;
      top: auto;
      bottom: auto;
      left: auto;
      right: auto;
      height: auto; } } }
.btn {
  display: inline-block;
  width: 160px;
  height: 46px;
  line-height: 46px;
  background: $text-color;
  color: $main;
  font-size: 14px;
  text-transform: uppercase;
  transition: background 300ms;
  &:hover {
    background: $text-color-dark; } }

// about
.about {
  width: 100%;
  height: 100%;
  display: inline-block;
  vertical-align: middle;
  .about__in {
    position: relative;
    height: 100%;
    font-size: 16px;
    @include r(480) {
      padding: 0 10px; } } }
.prev, .next {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	background: none;
	font-size: 28px;
	color: $border;
	transition: color 300ms;
	&:hover {
		color: $text-color-dark; } }
.prev {
  left: -20px;
  @include r(480) {
    left: -5px; } }
.next {
  right: -20px;
  @include r(480) {
    right: -5px; } }
.slider-wrap {
  padding-bottom: 40px;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  transition: opacity 300ms;
  &.loaded {
    opacity: 1;
    visibility: visible; } }
.slider {
  height: 100%;
  .slider__item {
    display: inline-block;
    float: none;
    height: 100%;
    @include vertical;
    cursor: hand; }
  .slider__in {
    display: inline-block;
    vertical-align: middle;
    width: 100%; }
  .slider__img {
    font-size: 0;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    background: $text-color;
    margin: 0 auto 5%;
    img {
      width: 150px;
      height: 150px;
      object-fit: cover; } }
  .slider__topper {
    position: relative;
    text-transform: uppercase;
    font-size: 2.4rem; }
  .slider__title {
    font-weight: bold; }
  .slider__content {
    font-size: 0;
    & > p {
      font-size: 1.6rem; } }
  .slider__col {
    width: 33.3%;
    display: inline-block;
    vertical-align: top;
    padding: 0 5%;
    text-align: left;
    font-size: 1.6rem;
    @include r(480) {
      font-size: 1.4rem;
      padding: 0 3%; } }
  .slider__mid {
    max-width: 240px;
    margin: 0 auto;
    font-size: 1.6rem; } }
.counter {
  position: absolute;
  top: -66px;
  right: -90px;
  color: $border;
  font-size: 4.6rem;
  line-height: 1;
  transform-origin: 100% 100%;
  transform: rotate(-90deg);
  .counter__current {
    font-size: 8rem; }
  @include r(768) {
    right: -100px; }
  @include r(680) {
    right: -90px; }
  @include r(600) {
    right: -80px; }
  @include r(568) {
    right: -70px; }
  @include r(480) {
    display: none!important; } }

.socials {
  font-size: 0;
  & > li {
    display: inline-block;
    vertical-align: middle;
    margin: 0 20px;
    @include r(1024) {
      margin: 0 10px; }
    @include r(360) {
      margin: 0 6px; } }
  .socials__link {
    display: inline-block;
    width: 60px;
    height: 60px;
    line-height: 72px;
    border-radius: 50%;
    background: $text-color;
    color: $main;
    text-align: center;
    transition: background 300ms;
    &:hover {
      background: $text-color-dark; }
    .icon {
      @include gpu; }
    .icon-fb {
      font-size: 18px; }
    .icon-tw, .icon-in {
      font-size: 16px; }
    .icon-pinterest, .icon-tumblr {
      font-size: 22px;
      margin-top: 18px; }
    @include r(600) {
      width: 40px;
      height: 40px;
      line-height: 55px;
      .icon-pinterest, .icon-tumblr {
        margin-top: 11px;
        font-size: 18px; } } } }


// contacts
.contacts {
  width: 100%;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  font-size: 16px;
  .contacts__mail {
    text-transform: uppercase;
    font-size: 2.4rem;
    color: $text-color;
    font-weight: normal; } }
.form {
	max-width: 400px;
	margin: 30px auto;
	.form__row {
		display: block;
		margin-bottom: 10px; } }
.input, .textarea {
	width: 100%;
	border: 2px solid $lightblue;
	font-size: 16px;
	color: $text-color-dark;
	@include placeholder {
		color: $text-color-dark; } }
.input {
	height: 42px;
	padding: 0 10px; }
.textarea {
	padding: 10px;
	resize: none; }

// portfolio
.portfolio {
  display: inline-block;
  width: 100%;
  height: 100%;
  font-size: 0;
  .portfolio__item {
    display: inline-block;
    vertical-align: top;
    width: 50%;
    height: 50%;
    padding: 15px; }
  .portfolio__link {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    background: #bddcff;
    transition: background 300ms;
    &:hover {
      background: #3984de;
      .portfolio__title {
        color: #fff; } }
    &.behance {
      &:hover {
        background: #0057ff; } }
    &.dribbble {
      &:hover {
        background: #ea4c89; } }
    &.creativemarket {
      &:hover {
        background: #8ba753; } }
    &.shutterstock {
      &:hover {
        background: #ee2924; } } }
  .portfolio__block {
    position: relative;
    height: 100%;
    @include vertical;
    img {
      display: inline-block;
      vertical-align: middle;
      height: 50%; }
    .icon {
      display: inline-block;
      vertical-align: middle;
      font-size: 70px;
      color: #fff;
      &.icon-dribbble {
        font-size: 74px; }
      &.icon-behance {
        font-size: 60px; } } }
  .portfolio__title {
    position: absolute;
    bottom: 5%;
    left: 5px;
    right: 5px;
    font-size: 16px;
    color: #07386e;
    transition: color 300ms; } }


